import React from 'react';

const GalleryV = () => {
    const videos = [
        { id: 1, src: './videos/ciduk quotes.mp4', alt: 'Ciduk Quotes' },
        { id: 2, src: './videos/obito tak segampang itu rock version edit.mp4', alt: 'Obito Tak Segampang Itu (rock version)' },
    ];

    return (
        <div className="galleryv-bg" id='gallery'>
            <div data-aos="zoom-in-up">
                <h1 className="gallery-font">Gallery (Video)</h1>
            </div>
            <div className="gallery" data-aos="zoom-out">
                {videos.map((video) => (
                    <div key={video.id}>
                        <video
                            src={video.src}
                            alt={video.alt}
                            controls
                            className="video"
                        />
                    </div>
                ))}
            </div>
        </div>
    );
};

export default GalleryV;
